header{font-size:1.2em;width:100%;position:relative;overflow-x:clip;margin:0 auto}@media (max-width: 482px){header{font-size:1em}}@media (min-width: 576px){header{font-size:1.3em}}@media (min-width: 992px){header{font-size:1.4em}}@media (min-width: 1440px){header{font-size:1.5em}}header #nav{height:1.33em;background:#00376A;color:white;font-family:"Share",cursive;text-transform:uppercase;margin:0 auto}@media (max-width: 330px){header #nav{height:auto}}header #info,header #info-small{background:#000509;color:#fff;padding:0.44em;transition:color 0.3s ease}header #info .icon,header #info-small .icon{font-size:1.4em;position:relative;top:0.15em;padding-right:0.3em;padding-left:0.3em}header #info:hover,header #info-small:hover{color:#006FD3;cursor:pointer}header #info{position:absolute;top:0;left:0}@media (max-width: 576px){header #info{display:none}}@media (min-width: 768px){header #info{display:none}}header #info-large{display:none}@media (min-width: 768px){header #info-large{display:inline}}header #info-small{display:none}@media (max-width: 576px){header #info-small{display:inline;padding:0.6em 0.2em 0.7em 0.2em}}header #language{position:absolute;right:0;top:-0.3em}@media (max-width: 482px){header #language{padding-right:0.25em}}header #language select{cursor:pointer;padding:0.4em;margin:0.2em;border-radius:0;background-color:#006FD3;border:none;border-bottom:2px solid #004B8E;color:white;appearance:none;-webkit-appearance:none;-moz-appearance:none;transition:color 0.3s ease, background-color 0.3s ease, border-bottom-color 0.3s ease}header #language select:hover,header #language select:focus{color:#00376A;border-bottom-color:#00376A}header ul{margin:0;padding:0.6em;text-align:center}@media (max-width: 576px){header ul{text-align:left}}@media (max-width: 330px){header ul{padding-right:2em}}@media (min-width: 768px){header ul{text-align:right;padding-right:3em}}header li{display:inline;padding:0.7em;cursor:pointer;transition:color 0.3s ease}@media (max-width: 482px){header li{padding-left:0.1em}}header li:hover{color:#006FD3}header #under-main-nav div{position:relative;z-index:1;display:inline-block}header .poletje-zima{background:#006FD3;-webkit-clip-path:polygon(0% 0%, 100% 0%, 70% 100%, 0% 100%);clip-path:polygon(0% 0%, 100% 0%, 70% 100%, 0% 100%);border-bottom-right-radius:85%;padding:0 0.6em;width:12em;transition:background 0.3s ease}header .poletje-zima:hover{background:#004B8E}@media (max-width: 576px){header .poletje-zima{-webkit-clip-path:none;clip-path:none;padding:0;border-bottom-right-radius:1em;width:auto}}header .poletje-zima p{padding:0.6em 0.3em;margin:0;display:inline-block;font-family:"Merienda",cursive;font-weight:bold}header .poletje-zima p span{transition:color 0.3s ease}header .poletje-zima p span:hover{cursor:pointer;color:#006FD3}header .menu{float:right;text-align:right;background:#006FD3;-webkit-clip-path:polygon(0% 0%, 100% 0%, 100% 100%, 30% 100%);clip-path:polygon(0% 0%, 100% 0%, 100% 100%, 30% 100%);border-bottom-left-radius:85%;padding:0 0.6em;width:12em;cursor:pointer;padding:0.6em 0.8em;transition:color 0.3s ease, background 0.3s ease}header .menu:hover{color:#006FD3;background:#004B8E}@media (max-width: 576px){header .menu{-webkit-clip-path:none;clip-path:none;border-bottom-left-radius:1em;width:auto}}header .menu span{margin:0;padding-right:0.3em;padding-left:0.3em}header #info-details{position:absolute;width:100%;top:0;z-index:3;left:-100%;background:#000509;color:white;text-align:center;transition:left 0.5s ease}header #info-details .hide-btn{cursor:pointer;font-size:1.5em;position:relative;top:0.3em;border-bottom:1px solid #fff;transition:color 0.3s ease, border-bottom 0.3s ease}header #info-details .hide-btn:hover{color:#006FD3;border-bottom:1px solid #006FD3}header #info-nav{position:relative;padding:1em;font-size:1.5em}header #info-nav span{padding:0.15em;margin:0.15em;cursor:pointer;fill:#fff;border-bottom:1px solid #000509;transition:color 0.3s ease, fill 0.3s ease, border-bottom 0.3s ease}header #info-nav span:hover{border-bottom:1px solid #006FD3;color:#006FD3;fill:#006FD3}header #info-nav .svg-icon{width:1.1em}@media (min-width: 768px){header #info-nav{position:static;padding:0;float:left;font-size:1.3em}header #info-nav span{border-bottom:none}header #info-nav span:hover{border-bottom:none}}header #menu-details{position:absolute;width:14em;top:2.54em;right:-14em;z-index:2;background:#006FD3;transition:right 0.5s ease}header #menu-details .hide-btn{cursor:pointer;text-align:center;font-size:1.5em;position:relative;padding-top:0.3em;border-bottom:1px solid #000;transition:color 0.3s ease, border-bottom 0.3s ease, background 0.3s ease}header #menu-details .hide-btn:hover{color:#006FD3;background:#004B8E;border-bottom:1px solid #00376A}header #menu-details ul{padding-bottom:1.3em;transition:background 0.3s ease}header #menu-details ul:hover{background:#004B8E}@media (min-width: 768px){header #menu-details ul{padding-right:1em}}header #menu-details ul li{display:block;padding:0.4em;border-bottom:1px solid #004B8E;transition:color 0.3s ease, border-bottom 0.3s ease}header #menu-details ul li:hover{color:#006FD3;border-bottom:1px solid #006FD3}header #logo{position:relative;top:-2.74em;text-align:center}@media (max-width: 576px){header #logo{text-align:right;right:3em}}@media (max-width: 330px){header #logo{text-align:left;top:0;left:1em}}@media (min-width: 992px){header #logo{text-align:left}}header #logo img{width:7em}@media (max-width: 482px){header #logo img{width:4.6em}}@media (min-width: 768px){header #logo img{width:9em}}@media (min-width: 992px){header #logo img{padding:3em 2em}}

